.articles-logo-container {
	display: flex;
	justify-content: left;
	padding-top: 25px;
}

.articles-logo {
	display: flex;
	position: fixed;
	border: 1px solid white;
	border-radius: 50%;
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.25);
	top: 4vh;
}

.articles-main-container {
	display: flex;
	flex-direction: column;
	height: 100%;
	margin: 0;
	padding-top: 120px;
}

.articles-container {
	display: flex;
	flex-direction: column;
}

.articles-wrapper {
	padding-top: 50px;
	padding-bottom: 50px;
	display: flex;
	flex-direction: column;
}

.articles-article {
	padding-top: 0px;
	padding-left: 35px;
	padding-bottom: 20px;
	border-left: 2px solid #f4f4f5;
	width: 100%;
}

@media (max-width: 1024px) {
	.articles-title {
		width: 100% !important;
	}

	.articles-subtitle {
		width: 100% !important;
	}
}

.articles-layout {
	display: flex;
	min-height: 100vh;
}

.articles-main-content {
	flex: 1;
	padding-left: 40px;
}

@media (min-width: 1024px) {
	.articles-main-content {
		margin-left: 250px;
	}
}

@media (max-width: 1024px) {
	.articles-main-content {
		padding-left: 0;
		padding-top: 60px;
	}

	.articles-layout {
		flex-direction: column;
	}
}